import Vue from 'vue'
import Router from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import index from '../views/index.vue' //首页


import emergency_video from '@/views/emergencyCommand/video.vue' //视频
import vehicleList from '@/views/emergencyCommand/vehicleList.vue' //车辆列表
import journal from '@/views/logbook/journal.vue' //行车日志
import drive from '@/views/assistedDriving/drive.vue' //辅助驾驶
import system from '@/views/system/system.vue' //系统管理 司机账号管理

import groundEnd from '@/views/system/groundEnd.vue' //系统管理 地面端账号管理
import notice from '@/views/system/notice.vue' //系统管理 公告
import typesetting from '@/views/system/typesetting.vue' //系统管理  排班管理
import electronics from '@/views/system/electronics.vue' //系统管理 电子手册

Vue.use(Router)
// 路由配置
const router = new Router({
  mode: 'hash', //去掉url中的#
  routes: [
    {
      path: '/Login',
      name: 'Login',
      meta: {
        title: '登录'
      },
      component: () => import('@/views/Login.vue')
    },
    {
      path: '/',
      name: 'Home',
      component: Home,
      meta: {
        title: '首页',
        requiresAuth: true, // true表示需要登录
      },
      children: [
        {
          path: "/emergency_video",
          component: emergency_video,
          hidden: true
        },
        {
          path: "/vehicleList",
          component: vehicleList,
          hidden: true
        },
        {
          path: "/index",
          component: index,
          hidden: true
        },
        {
          path: "/journal",
          component: journal,
          hidden: true
        },
        
        {
          path: "/drive",
          component: drive,
          hidden: true
        },
        {
          path: "/system",
          component: system,
          hidden: true
        },
        {
          path: "/groundEnd",
          component: groundEnd,
          hidden: true
        },
        {
          path: "/notice",
          component: notice,
          hidden: true
        },
        {
          path: "/typesetting",
          component: typesetting,
          hidden: true
        },
        {
          path: "/electronics",
          component: electronics,
          hidden: true
        },

      ]
    },

  ]
})

// 路由拦截，判断是否需要登录
router.beforeEach((to, from, next) => {
  let user = JSON.parse(localStorage.getItem("user")) ? JSON.parse(localStorage.getItem("user")) : {};
  console.log(user,'aaa')
  // return
  // if (to.path == "Login") {
  //   next()
  // }else {
    if(to.name!=='Login'){
      if(user && user.token) {
        next()
      }else {
        next('/Login')
      }
    }else{
      next()
    }
    
  // }
})

export default router
