  },
  mounted() {
    this.getCurrentTime();
  },
  methods: {
    getCurrentTime() {
      //获取当前时间并打印
      var _this = this;
      let yy = new Date().getFullYear();
      let mm = new Date().getMonth() + 1;
      let dd = new Date().getDate();
      let hh = new Date().getHours();
      let mf =
        new Date().getMinutes() < 10
          ? "0" + new Date().getMinutes()
          : new Date().getMinutes();
      let ss =
        new Date().getSeconds() < 10
          ? "0" + new Date().getSeconds()
          : new Date().getSeconds();
      let times = hh + ":" + mf + ":" + ss;
      this.dataList.forEach((tiem) => {
        // console.log(new Date(tiem.val).getTime());
        // console.log(new Date(times).getTime());
        console.log(tiem.val);
        console.log(times);
