<script>
import { loginGo, getChannel } from "@/views/utils/api";

export default {
  data() {
    return {
      
      input2: "",
      option: {
        appId: "36bfd34ea5ac412d98fd7a4e173ee27a",
      },
      params: {
        uid: "",
      },
      // client: null,
      peerId: "",
      num: 1
      
    };
  },
  async mounted() {
    this.$http.defaults.headers.common["token"] =  JSON.parse(localStorage.getItem('user')) ? JSON.parse(localStorage.getItem('user')).token : '' 
    
        // if(this.num <= 1) {
    // let res = 
    // console.log(this.client,'aaa')
        // }
  },
  methods: {
    // 呼叫
    async dialSend() {
      let res = await getChannel("Record/getChannel");
      if (res && res.status == 200) {
        if(!res.data.data.sn) {
          this.$message.error('房间号获取失败')
          return
        }
        localStorage.setItem('sn',res.data.data.sn)
        // let user = JSON.parse(localStorage.getItem("user")) ? JSON.parse(localStorage.getItem("user")) : {};
        // // if(this.num <= 1) {
        //   await this.client.login({ token: null, uid: user.id.toString() });
        // }
        this.num++
        // 发送消息 text：发送内容 ，对方uid
        let obj = JSON.stringify({
          avatar: 'https://image.baidu.com/search/detail?ct=503316480&z=undefined&tn=baiduimagedetail&ipn=d&word=%E5%9B%BE%E7%89%87&step_word=&ie=utf-8&in=&cl=2&lm=-1&st=undefined&hd=undefined&latest=undefined&copyright=undefined&cs=3693712532,3077493988&os=1898649515,2536979137&simid=3400603706,282916162&pn=43&rn=1&di=7077213605308923905&ln=1655&fr=&fmq=1652797529140_R&fm=&ic=undefined&s=undefined&se=&sme=&tab=0&width=undefined&height=undefined&face=undefined&is=0,0&istype=0&ist=&jit=&bdtype=0&spn=0&pi=0&gsm=0&objurl=https%3A%2F%2Fgimg2.baidu.com%2Fimage_search%2Fsrc%3Dhttp%253A%252F%252Fimg.jj20.com%252Fup%252Fallimg%252F511%252F101611154647%252F111016154647-10-1200.jpg%26refer%3Dhttp%253A%252F%252Fimg.jj20.com%26app%3D2002%26size%3Df9999%2C10000%26q%3Da80%26n%3D0%26g%3D0n%26fmt%3Dauto%3Fsec%3D1655389530%26t%3Deb83cadd8fddcd3a4ad9c07b6ad25db1&rpstart=0&rpnum=0&adpicid=0&nojc=undefined&dyTabStr=MCwzLDYsMSw0LDIsNSw3LDgsOQ%3D%3D',
          messageId: 1,
          name: '张三',
          role: '地面端',
          channelId: res.data.data.sn
        })
        this.client.sendMessageToPeer({ text: obj }, "1");
        
        this.$router.push({
          path: 'emergency_video',
          query: {
            sn: res.data.data.sn
          }
        })
      } else {
        this.$message.error("sn获取失败请重试");
      }
    },
    
    // 登录
    async loginClick() {
      let formData = new FormData();
      formData.append("account", "002");
      formData.append("password", "a123456");
      let res = await loginGo("user/login", formData);
      if (res.status == 200) {
        // 存储自己uid
        localStorage.setItem("user", JSON.stringify(res.data.data));
        // if(user.uid) {
          
        // }
        
      } else {
        this.$message.error("登录失败");
      }
    },
    //跳转
    async jumpVideo() {
      // this.$router.push('emergency_video')
      this.dialSend()
    },
    //
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
  },
};
</script>

